import { useRef, useState } from "react"

export const useFileInput = ({ data, urlKey, fileKey, set, fileSize }) => {
  const [loading, setLoading] = useState(false)
  const fileInputRef = useRef()

  const clickUpload = () => {
    fileInputRef.current.click()
    setLoading(true)
  }

  const setFile = (file) => {
    const currentUrl = URL.createObjectURL(file)

    const temp = 
      urlKey ? 
        { ...data,  
          [fileKey]: file,
          [urlKey]: currentUrl,
        }
      :
        { ...data,  
          [fileKey]: file,
        }
      
    set({...temp})
  }

  const handleFileInput = (e) => {
    const fileList = e.target.files
    const currentFile = fileList[0]

    // fileSize는 KB 단위 : float
    if (fileSize) {
      if (currentFile?.size > (fileSize * 1024)){
        alert(`파일 크기는 ${fileSize}KB를 초과 할 수 없습니다.`)
        
      } else {
        setFile(currentFile)
      }
    } else {
      setFile(currentFile)
    }

    setLoading(false)
  }

  return [ handleFileInput, fileInputRef, clickUpload, loading ]
}
