import React from 'react'
import { styled } from '@mui/system';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import PopupPostCode from './PopupPostCode';

const BoxStyle = styled(Box)(({ theme, width, height }) => ({
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: width,
  height: height,
  display: 'flex',
  flexDirection: 'column',
  padding: '40px 30px',
  backgroundColor: '#ffffff',
  borderRadius: '10px',
  boxShadow: 24,
  overflow:'auto'
}));

const ModalAddress = ({ open, onClose, setValue, width, height }) => {

  return (
    <>
      <Modal
        open={open}
        onClose={onClose}
      >
        <BoxStyle width={width || 700} height={height || 520}>
          <PopupPostCode setValue={setValue} onClose={onClose} />

      </BoxStyle>
    </Modal>
    </>
  )
}

export default ModalAddress