import React from "react";
import { useMutation } from "@tanstack/react-query";
import { createDojang } from "../../../../../api/Dojang/dojangApi";
import ModalAddress from "../../../../../components/common/PostCode/ModalAddress";
import BlackBtnSM from "../../../../../components/UI/Atoms/Button/BlackBtnSM";
import { useModal } from "../../../../../hooks/basic/useModal";
import { useInputs } from "../../../../../hooks/input/useInputs";
import { getFormData } from "../../../../../utils/basic/getFormData";
import { formDataDebug } from "../../../../../utils/debug/debug";
import InfoBox from "../DojangInfo/components/InfoBox";
import { InfoInput, InfoLabel } from "../DojangInfo/components/InfoLable";
import { Col, Input, Row } from "../../Student/component/StudentInfo/StudentForm/StudentForm";
import EditBtnSM from "../../../../../components/UI/Atoms/Button/EditBtnSM";
import ModalContainer from "../../../../../components/layout/ModalContainer";
import SecondBtnSM from "../../../../../components/UI/Atoms/Button/SecondBtnSM";
import BottomDescription from "../../../../../components/common/Form/BoxForm/BottomDescription";
import { useFileInput } from "../../../../../hooks/file/useFileInput";
import LogoUploadBtn from "../DojangInfo/components/LogoUploadBtn";
import styled from '@mui/material/styles/styled';
import { Box } from '@mui/material';
import { useLocation, useNavigate } from "react-router-dom";
import { businessNumberHyphenRegex } from "../../../../../utils/lib/Regex";



const ImageBox = styled(Box)(({ src, shape, theme }) => ({
  width: '320px',
  height: '180px',
  backgroundImage: `url(${src})`,
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'contain',
  backgroundPosition: 'center',
  border: '1px solid',
  borderColor: '#DDDDDD',
  transition: 'all 0.3s ease',
}));



const ResistDojang = ({ open, onClose }) => {
  const nav = useNavigate()
  const location = useLocation()

  // custom hook
  const [openAdr, openAdrFn, closeAdrFn] = useModal();

  const [editData, setEditData, handleChange] = useInputs({
    name: '',
    phone_number: '',
    address_name: '',
    address_detail: '',
    BR: '',
    BR_number: '',
    logo_img: '',
    logo: '',
  });

  const [handleFileInput, fileInputRef, clickUpload] = useFileInput({
    data: editData,
    fileKey: 'BR_img',
    set: setEditData,
  });

  const [handleImgInput, imgInputRef, clickUploadImg] = useFileInput({
    data: editData,
    urlKey: 'logo_img',
    fileKey: 'logo',
    set: setEditData,
  });

  // query
  const { isLoading, mutate } = useMutation((body) => createDojang(body), {
    onSuccess: () => {
      alert('성공적으로 등록되었습니다. \n새로운 도장을 선택해보세요!')
      if (location?.pathname !== '/select/dojang') {
        nav('/select/dojang')
      } else {
        setTimeout(() => window.location.reload(), 500)
      }
    },
    onError: () => alert('서버 오류가 발생하였습니다. \n잠시 후 다시 시도해주세요.'),
  });


  // handler
  const handleSave = () => {
    const { BR, BR_img, logo_img, logo, zone_code, ...postData } = editData;

    if (postData?.name === '') {
      alert('도장 이름을 입력해주세요.')
      return;
    }
    if (postData?.phone_number === '') {
      alert('전화번호를 입력해주세요.')
      return;
    }
    if (postData?.address_name === '') {
      alert('주소를 입력해주세요.')
      return;
    }
    if (postData?.address_detail === '') {
      alert('상세 주소를 입력해주세요.')
      return;
    }
    if (postData?.BR_number === '') {
      alert('사업자 등록번호를 입력해주세요.')
      return;
    }
    if (BR_img && logo) {
      const formData = getFormData([
        ...Object.entries(postData),
        ['BR_img', BR_img],
        ['logo', logo]
      ]);
      // formDataDebug(formData);
      mutate(formData);
    } else {
      alert('사업자 등록증과 로고를 등록해주세요.')
      return;
    }

  };


  //   useDebug(editData)
  return (
    <>
      <ModalContainer
        title='도장 등록'
        open={open}
        onClose={onClose}
        width={700}
        height={900}
      >
        <InfoBox
          title="도장 정보"
          className="w-[600px] mt-[20px] p-[20px] m-auto border border-[1px] border-solid border-[#eeeeee] border-t-0 border-x-0 rounded-none"
        >
          <div className='mt-[25px]'>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>도장 이름 *</InfoLabel>
                <div>
                  <InfoInput
                    name="name"
                    placeholder='도장 이름을 입력해주세요.'
                    value={editData?.name}
                    onChange={handleChange}
                    required
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>도장 전화번호 *</InfoLabel>
                <div>
                  <InfoInput
                    name="phone_number"
                    placeholder="'-' 없이 입력해주세요."
                    value={editData?.phone_number}
                    onChange={handleChange}
                    inputProps={{ maxLength: 11}}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>도장 주소 *</InfoLabel>
                <div>
                  <InfoInput
                    name="address_name"
                    placeholder='주소 검색'
                    value={editData?.address_name}
                    onChange={handleChange}
                    onClick={openAdrFn}
                  />
                  <ModalAddress open={openAdr} onClose={closeAdrFn} setValue={setEditData}></ModalAddress>
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel></InfoLabel>
                <div>
                  <InfoInput
                    name="address_detail"
                    placeholder='상세 주소'
                    value={editData?.address_detail}
                    onChange={handleChange}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px]">
                <InfoLabel>사업자 등록번호 *</InfoLabel>
                <div>
                  <InfoInput
                    name="BR_number"
                    placeholder='사업자 등록번호를 입력해주세요.'
                    value={businessNumberHyphenRegex(editData?.BR_number)}
                    onChange={handleChange}
                    inputProps={{ maxLength: 12 }}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col className="mb-[10px] mt-[-20px]">
                <InfoLabel>사업자 등록증 *</InfoLabel>
                <div className="flex flex-col justify-center relative gap-[10px]">
                  <input
                    ref={fileInputRef}
                    type="file"
                    accept="image/*, .pdf"
                    onChange={handleFileInput}
                    className="hidden"
                  />
                  <div className='flex gap-[15px] items-cneter pt-[30px]'>
                    <SecondBtnSM className='w-[100px] text-[#484848] border-[#484848]' onClick={clickUpload}>파일 업로드</SecondBtnSM>
                    <Input value={editData?.BR_img?.name || ''} placeholder='파일을 업로드 해주세요.' disabled/>
                  </div>
                  <BottomDescription>
                    * 사업자 등록증은 PDF, JPG, PNG 파일을 권장합니다.
                  </BottomDescription>
                </div>
              </Col>
            </Row>
            <Row className='mt-[20px] mb-[10px]'>
              <Col>
                <div className='flex'>
                  <div className='flex flex-col'>
                    <InfoLabel>도장 로고 *</InfoLabel>
                      <BottomDescription>
                        권장사이즈는 16 : 9 입니다.
                      </BottomDescription>
                      <div className="flex gap-2 my-[10px]">
                        <LogoUploadBtn onClick={clickUploadImg}>+ 사진 올리기</LogoUploadBtn>
                      </div>
                  </div>

                  <div className="ml-[8px] w-[334px] bg-[#ffffff]">
                    <input
                      ref={imgInputRef}
                      type="file"
                      accept="image/*"
                      onChange={handleImgInput}
                      className="hidden"
                    />
                    <ImageBox src={editData?.logo_img ? editData?.logo_img : ''} />
                  </div>
                </div>
              </Col>
            </Row>
            <div className="flex justify-center gap-[10px] mt-[40px] mb-[10px]">
              <EditBtnSM onClick={onClose}>취소</EditBtnSM>
              <BlackBtnSM onClick={handleSave} disabled={isLoading}>등록</BlackBtnSM>
            </div>

          </div>
        </InfoBox>
      </ModalContainer>
    </>
  );
};

export default ResistDojang;
