import DaumPostcode from 'react-daum-postcode';

const PopupPostCode = ({ setValue, onClose, ...props }) => {
  // 우편번호 검색 후 주소 클릭 시 실행될 함수, data callback 용

  const handlePostCode = (data) => {
    let depthAddr = data.jibunAddress.split(' ');
    let addressInfo = {
      address_name: data.jibunAddress,
      region_1depth_name: depthAddr[0],
      region_2depth_name: depthAddr[1],
      region_3depth_name: depthAddr[2],
      road_address: data.roadAddress,
      zone_code: data.zonecode,
    };
    console.log(addressInfo);
    setValue((prev) => {
      return {
        ...prev,
        ...addressInfo,
      };
    });
    onClose();
  };

  // const handleClose = () => {
  //   const [selected, setSelected] = useState(false);

  //   if (selected) {
  //     setSelected()
  //   }
  // }

  return (
    <div className="flex flex-col justify-center items-center">
      <DaumPostcode onComplete={handlePostCode} />
    </div>
  );
};

export default PopupPostCode;
